#tableManageUser {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: auto;
    width: 100%;
  }
  
  #tableManageUser td, #tableManageUser th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  #tableManageUser tr:nth-child(even){background-color: #f2f2f2;}
  
  #tableManageUser tr:hover {background-color: #ddd;}
  
  #tableManageUser th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #04aa7b;
    color: white;
  }