#customers {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: auto;
    margin: 20px;
  }
  
  #customers td, #customers th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  #customers tr:nth-child(even){background-color: #f2f2f2;}
  
  #customers tr:hover {background-color: #ddd;}
  
  #customers th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #04aa7b;
    color: white;
  }
  .modal-user-container {
    .modal-content {
      .modal-header {
        button {
          background-color: transparent !important;
          color: white;
          border: red none;
          font-size: 27px;
        }
      }
    }
  }
  .modal-user-body {
    display: flex;
    gap: 15px;
    flex-wrap: wrap;
  
    .input-container {
      display: flex;
      flex-direction: column;
      width: 49%;
      &.max-width-input {
        width: 100%;
      }
  
      input {
        border-radius: 3px;
        border: 1px solid grey;
        height: 30px;
        outline: none;
        padding: 0 10px;
      }
    }
  }